import request from '@/utils/request'
import { ouchnCustom } from "@/service"

// 我的资源查询我的书架
export function selectMyResourceListGoods(query){
  return request({
    url:ouchnCustom + '/web/myResource/selectMyResourceListGoods',
    method:'get',
    params: query
  })
}
// 删除我的书架
export function myResourceremove(query){
  return request({
    url:ouchnCustom + '/web/myResource/remove',
    method:'post',
    params: query
  })
}
